import { Event, Component, EventEmitter, h, Prop, Host } from '@stencil/core';
import { cva } from 'class-variance-authority';

export type LabelPosition = 'before' | 'after';

const variants = cva(
    'group/switch outline-active hover:bg-surface-1 focus:bg-surface-1 flex w-fit cursor-pointer items-center gap-8 rounded-full rounded-md p-8 outline-0 outline-none',
    {
        variants: {
            disabled: {
                false: null,
                true: 'pointer-events-none opacity-50 grayscale-[1]',
            },
            labelPosition: {
                before: 'flex-row',
                after: 'flex-row-reverse',
            },
        },
    },
);

const trackVariants = cva(
    'absolute top-0 right-0 bottom-0 left-0 flex h-[20px] w-[40px] items-center justify-center rounded-full align-middle [transition:0.2s]',
    {
        variants: {
            disabled: {
                false: 'shadow-inset-xs cursor-pointer',
                true: 'shadow-inset-xs cursor-default',
            },
            active: {
                false: 'border-med bg-surface-2 border border-solid',
                true: 'bg-active-foreground/30 group-focus-within/switch:ring-active-foreground group-focus-within/switch:ring-1',
            },
        },
    },
);

const thumbVariants = cva(
    'material-icons text-icon-md absolute relative m-2 flex h-[16px] w-[16px] scale-1 items-center justify-center rounded-full text-center text-white select-none ' +
        'transition-all duration-300 ease-in-out',
    {
        variants: {
            active: {
                false: 'bg-disabled-dark translate-x-[-10px]',
                true: 'bg-active-foreground group-focus-within/switch:ring-active-foreground/30 translate-x-[10px] group-focus-within/switch:ring-[6px]',
            },
            disabled: {
                false: 'cursor-pointer',
                true: null,
            },
        },
    },
);

/**
 * @category Form Controls
 * @description A toggle switch component for binary on/off selections. Provides accessible alternative to checkboxes with visual toggle behavior.
 */

@Component({
    tag: 'atui-toggle-switch',
    shadow: false,
})
export class AtuiToggleSwitchComponent {
    /**
     * Label displayed alongside the toggle.
     */
    @Prop()
    label?: string;

    /**
     * Short description or validation hint if required.
     */
    @Prop()
    hint_text?: string;

    /**
     * Position of the label, left or right of the toggle.
     */
    @Prop()
    label_position: LabelPosition = 'before';

    /**
     * Show the toggle label.
     */
    @Prop()
    show_label?: boolean = true;

    /**
     * Disable interaction with the toggle.
     */
    @Prop()
    disabled?: boolean;

    /**
     * Set the toggles state.
     */
    @Prop({ mutable: true })
    value: boolean = false;

    inputEl: HTMLInputElement;

    private toggleId = `toggle-${Math.random().toString(36).substring(2, 11)}`;

    /**
     * Emits an event when toggled with `event.detail` being true if the switch is enabled
     */
    @Event()
    atuiChange: EventEmitter<boolean>;

    onToggle(change: boolean): void {
        this.value = change;
        this.atuiChange.emit(change);
    }

    render() {
        const classname = variants({
            disabled: this.disabled,
            labelPosition: this.label_position,
        });

        const thumbClassname = thumbVariants({
            active: this.value,
            disabled: this.disabled,
        });

        const trackClassname = trackVariants({
            active: this.value,
            disabled: this.disabled,
        });

        return (
            <Host
                id={`${this.toggleId}-host`}
                role="switch"
                aria-labelledby={`${this.toggleId}-label`}
                class={classname}
                tabIndex={0}
                onClick={() => this.inputEl.click()}
                onKeyDown={(event) => {
                    if (event.key === ' ' || event.key === 'Enter') {
                        event.preventDefault();
                        this.inputEl.click();
                    }
                }}
            >
                <div class="flex flex-col">
                    <slot name="label" />
                    {!!this.label && this.show_label && (
                        <atui-form-label
                            label={this.label}
                            class="pointer-events-none select-none"
                            for={this.toggleId}
                            id={`${this.toggleId}-label`}
                            data-name="switch-label"
                        ></atui-form-label>
                    )}
                    {this.hint_text && (
                        <span
                            class="text-light inline-block text-xs leading-tight"
                            data-name="select-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                </div>

                <div class="relative flex h-20 w-40">
                    <input
                        id={this.toggleId}
                        class="h-0 w-0 opacity-0"
                        disabled={this.disabled}
                        type="checkbox"
                        checked={this.value}
                        tabIndex={-1}
                        onChange={(e) =>
                            this.onToggle(
                                (e.target as HTMLInputElement).checked,
                            )
                        }
                        ref={(el) => (this.inputEl = el)}
                        data-name="switch-input"
                    />
                    <span class={trackClassname}>
                        <span class={thumbClassname}>
                            {this.value ? 'check' : 'remove'}
                        </span>
                    </span>
                </div>
            </Host>
        );
    }
}
